<template>
  <div class="post">
    <div class="layer layer-1">
      <h1 class="post-title">{{ this.postData.title }}</h1>
    </div>
    <div class="layer layer-2">
      <div class="post-content" v-html=postContent></div>
    </div>
  </div>
</template>

<script>
import simplemde from 'simplemde';

export default {
  name: 'post',

  beforeMount() {
  
    this.populatePost();
  },
  watch: {
    '$route'() {
      
      this.populatePost();
    }
  },

  data: () => ({
    
    postContent: '',
    postData: {
      markdownContent: '',
      title: '',
      postViews: 0,
      postAppreciations: 0,
      postKeywords: [],
      
      owner: {
        profileAsset: {
          path: ''
        },
        userName: '',
        name: '',
        description: '',
        privilegeStatus: ''
      }
    }

  }),


  methods: {
    populatePost() {
      
      this.$store.dispatch( 'populatePost', {
        userName: this.$route.params.username,
        postId: this.$route.params.postid

      }).then( postData => {
        
        this.postData = postData;
        this.postContent = simplemde.options.previewRender( this.postData.markdownContent );

      })
    }
  }
}
</script>

<style>
</style>



